import preview from './templet/preview.vue';
import titlePurchase from './title/titlePurchase.vue';
import simple from './templet/simple.vue';
import general from './templet/general.vue';
import moreSet from './templet/moreSet/moreSet.vue';
import noEditBtn from './btn/noEditBtn.vue';
import { mapMutations, mapGetters } from 'vuex';
export default {
	name: 'dataReport',
	components: {
		preview,
		titlePurchase,
		simple,
		general,
		moreSet,
		noEditBtn
	},
	data() {
		return {
			loadEnd: false, // 判断所有接口是否加载完成
			caiGouList: {} // 采购数据
		};
	},
	onLoad() {
		console.log('加载发布采购');
		this.caiGouList = this.GET_PUBLISH.publishPurchase.caiGouList;
		this.caiGouList.purcEndTime = this.getDate(3, 'later'); // 报价截至时间，默认三天后又截至
		this.getCaiGouTemplate();
	},
	computed: {
		...mapGetters(['GET_PUBLISH'])
	},
	watch: {
		GET_PUBLISH: {
			handler(a, b) {
				this.caiGouList = a.publishPurchase.caiGouList;
			},
			deep: true
		}
	},
	methods: {
		...mapMutations({
			setTempletType: 'setTempletType',
			setCaiGouList: 'setCaiGouList'
		}),
		clickBusinessPutIn() {
			this.$store.commit('setTempletType', 0);
			console.log('触发商机投放组件按钮');
		},
		getCaiGouTemplate() {
			console.log('记载用户默认采购模板');
			let params = {};
			if (!uni.getStorageSync('landRegist')) {
				this.landRegistra(); // 判断登录状态
			} else if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				uni.showLoading({
					// 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/purc/getDefault?userId=' + landRegistLG.user.id, //接口地址。
					data: params,
					method: 'GET',
					header: {
						Authorization: 'Bearer ' + landRegistLG.token //将token放到请求头中
					},
					success: response => {
						if (response.data.ret === '200') {
							uni.hideLoading(); // 隐藏 loading
							let content = response.data.content;
							this.caiGouList.purcType = content.purcType;
							this.caiGouList.paraList = content.paraList;
							this.caiGouList.requList = content.requList;
							if (this.caiGouList.purcType === '0') { // 设置简单模板
								this.$store.commit('setTempletType', 0);
							} else if (this.caiGouList.purcType === '1') { // 设置普通模板
								this.$store.commit('setTempletType', 1);
							};
							this.$store.commit('setCaiGouList', this.caiGouList); // 跟新采购列表数据
							this.loadEnd = true; // 加载完成
						} else {
							uni.hideLoading(); // 隐藏 loading
							uni.showToast({
								title: '网络开小差了，请稍后重试',
								icon: 'none',
								duration: 1000
							});
						}
					},
					fail: error => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		}
	}
};
